@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cabin&display=swap');


@layer base {
    :root {
        /* colors */
        --primary-50: #e0fcff;
        --primary-100: #bef8fd;
        --primary-200: #87eaf2;
        --primary-300: #54d1db;
        --primary-400: #38bec9;
        --primary-500: #2cb1bc;
        --primary-600: #14919b;
        --primary-700: #0e7c86;
        --primary-800: #0a6c74;
        --primary-900: #044e54;
      
        /* grey */
        --grey-50: #f0f4f8;
        --grey-100: #d9e2ec;
        --grey-200: #bcccdc;
        --grey-300: #9fb3c8;
        --grey-400: #829ab1;
        --grey-500: #627d98;
        --grey-600: #486581;
        --grey-700: #334e68;
        --grey-800: #243b53;
        --grey-900: #102a43;
        /* rest of the colors */
        --black: #222;
        --white: #fff;
        --red-light: #f8d7da;
        --red-dark: #842029;
        --green-light: #d1e7dd;
        --green-dark: #0f5132;
      
        /* fonts  */
        
       
        --small-text: 0.875rem;
        --extra-small-text: 0.7em;
        /* rest of the vars */
        --backgroundColor: var(--grey-50);
        --textColor: var(--grey-900);
        --borderRadius: 0.25rem;
        --letterSpacing: 1px;
        --transition: 0.3s ease-in-out all;
        --max-width: 1120px;
        --fixed-width: 500px;
        --fluid-width: 90vw;
        --breakpoint-lg: 992px;
        --nav-height: 6rem;
        /* box shadow*/
        --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
        --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
          0 2px 4px -1px rgba(0, 0, 0, 0.06);
        --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
          0 4px 6px -2px rgba(0, 0, 0, 0.05);
        --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
          0 10px 10px -5px rgba(0, 0, 0, 0.04);
      }
    html {
        font-size: 62.5%;
    }
    body {
      font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 1.5rem;
        line-height: 1.75;
        background-color: #F0F4F8;
        color: #102a43;
    }
    h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-weight: 400;
  line-height: 1.3;
  text-transform: capitalize;
  letter-spacing: var(--letterSpacing);
}
h1 {
    margin-top: 0;
    font-size: 3.052rem;
  }
  
  h2 {
    font-size: 2.441rem;
  }
  
  h3 {
    font-size: 1.953rem;
  }
  
  h4 {
    font-size: 1.563rem;
  }
  
  h5 {
    font-size: 1.25rem;
  }

  .family {
    font-family: 'Roboto Condensed', sans-serif;
  }
  .cabin {
    font-family: 'Cabin', sans-serif;
  }

 
  
  .alert-danger {
    color: var(--red-dark);
    background: var(--red-light);
  }
  
  .alert-success {
    color: var(--green-dark);
    background: var(--green-light);
  }
}

@layer components {
  .btn {
    
    text-transform: capitalize;
    transition: all ease-in-out 0.2s;
    
    font-size: 2rem;
  }
  .btn-default {
    background-color: #289daa;
    color: white;
    padding: 0.3rem 2.2rem;
    border-radius: 0.6rem;
  }
  .btn-error {
    color: #289daa;
    text-decoration: underline;
  }

  .btn-error:hover {
    
    color: #14565e;
  }

  .btn-default:hover {
    background-color: #14565e;
    
  }
  .alert {
    padding: 0.375rem 0.75rem;
    border-color: transparent;
    border-radius: var(--borderRadius);
    text-align: center;
    letter-spacing: var(--letterSpacing);
  }
}